<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form :model="form">
        <el-form-item label="规格名称" :label-width="formLabelWidth">
          <el-input v-model="form.specsname"></el-input>
        </el-form-item>
        <el-form-item
          v-for="(item,index) in attrArr"
          :key="index"
          label="规格属性"
          :label-width="formLabelWidth"
        >
          <el-col :span="18">
            <el-input v-model="item.value"></el-input>
          </el-col>
          <el-button type="primary" v-if="index==0" @click="addAttr">添加规格属性</el-button>
          <el-button type="danger" v-else @click="delAttr(index)">删除</el-button>
        </el-form-item>
        <el-form-item label="规格状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="skyblue"
            inactive-color="#ff4949"
            :active-value="1"
            :inactive-value="2"
          ></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="confirm" v-if="info.isAdd">确 定</el-button>
        <el-button type="primary" @click="update" v-else>修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import { warningAlert, successAlert } from "../../../utils/alerts";
import { addSpec, getOneSpec, updateSpec } from "../../../utils/req";
export default {
  props: ["info"],
  data() {
    return {
      //规格属性值设定
      attrArr: [{ value: "" }],
      formLabelWidth: "120px",
      form: {
        specsname: "",
        attrs: "",
        status: 1,
      },
    };
  },
  methods: {
    ...mapActions({
      reqSpecList: "spec/specListActions",
      reqCount: "spec/countActions",
    }),
    //   取消
    cancel() {
      (this.info.show = false),
        (this.attrArr = [{ value: "" }]),
        (this.form = {
          specsname: "",
          attrs: "",
          status: 1,
        });
      this.attrArr = [{ value: "" }]; //规格属性值
    },
    // 点击添加规格属性
    addAttr() {
      this.attrArr.push({ value: "" });
    },
    // 删除规格属性
    delAttr(index) {
      this.attrArr.splice(index, 1);
    },
    confirm() {
      // 处理规格属性
      if(this.attrArr.some(item=>item.value === '')){
        warningAlert("规格属性值不能为空");
        return;
      }
      this.form.attrs = this.attrArr.map(item=>item.value).join(',');
      addSpec(this.form).then((res) => {
        successAlert(res.data.msg);
        this.cancel();
        this.reqSpecList();
        this.reqCount();
      });
    },
    getDetail(id) {
      getOneSpec({ id }).then((res) => {
        this.form = res.data.list[0];
        this.form.id = id;
        // 属性为数组 所以 将其分割遍历
        let arr = this.form.attrs.split(",");
        this.attrArr = [];
        for (let i in arr) {
          // //规格属性值为 attrArr: [{ value: "" }],
          this.attrArr.push({ value: arr[i] });
        }
      });
    },
    update() {
      // 处理规格属性
      if(this.attrArr.some(item=>item.value === '')){
        warningAlert("规格属性值不能为空");
        return;
      }
      this.form.attrs = this.attrArr.map(item=>item.value).join(',');
      updateSpec(this.form).then((res) => {
        this.cancel();
        successAlert(res.data.msg);
        this.reqSpecList();
      });
    },
  },
};
</script>

<style>
</style>


